.App {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#001f4b), to(#FFAE00));
  background-image: -webkit-linear-gradient(top, #001f4b 0%, #FFAE00 100%);
  background-image: -o-linear-gradient(top, #001f4b 0%, #FFAE00 100%);
  background-image: linear-gradient(to bottom, #001f4b 0%, #FFAE00 100%);
  height: auto;
  overflow: auto;
  font-family: 'Spectral SC', serif;
}

h1 {
  font-family: 'Spectral SC', serif;

}

#mainLogo {
  height: 150px;
  width: 300px;
}

.appContainer {
  padding: 2px 16px;
  text-align: center;
}



